<template>
  <div class="noticeView"></div>
</template>

<script>
export default {
  name: "noticeView",
  data() {
    return {};
  },
  methods: {
    update() {
      this.$http
        .get("/pettime/rest/notice/view/" + this.$route.query.uid)
        .then((res) => {
          console.log(res.data);
        });
    },
  },
  created() {
    this.update();
  },
};
</script>

